import { evntBus } from '../../bus';
export default {
  data: () => ({
    dialog: false,
    singleSelect: true,
    selected: [],
    dialog_data: '',
    company: '',
    customer: '',
    mode_of_payment: '',
    full_name: '',
    mobile_no: '',
    headers: [
      {
        text: __('Full Name'),
        value: 'full_name',
        align: 'start',
        sortable: true,
      },
      {
        text: __('Mobile No'),
        value: 'mobile_no',
        align: 'start',
        sortable: true,
      },
      {
        text: __('Amount'),
        value: 'amount',
        align: 'start',
        sortable: true,
      },
      {
        text: __('Date'),
        align: 'start',
        sortable: true,
        value: 'posting_date',
      },
    ],
  }),
  watch: {},
  methods: {
    close_dialog() {
      this.dialog = false;
    },
    search_by_enter(e) {
      if (e.keyCode === 13) {
        this.search();
      }
    },
    search() {
      const vm = this;
      frappe.call({
        method: 'posawesome.posawesome.api.m_pesa.get_mpesa_draft_payments',
        args: {
          company: this.company,
          mode_of_payment: this.mode_of_payment,
          mobile_no: this.mobile_no,
          full_name: this.full_name,
        },
        async: false,
        callback: function (r) {
          if (!r.exc) {
            vm.dialog_data = r.message;
          }
        },
      });
    },
    submit_dialog() {
      const vm = this;
      if (this.selected.length > 0) {
        const selected_payment = this.selected[0].name;
        frappe.call({
          method: 'posawesome.posawesome.api.m_pesa.submit_mpesa_payment',
          args: {
            mpesa_payment: selected_payment,
            customer: this.customer,
          },
          async: false,
          callback: function (r) {
            if (!r.exc) {
              evntBus.$emit('set_mpesa_payment', r.message);
              vm.dialog = false;
            }
          },
        });
      }
    },
    formtCurrency(value) {
      value = parseFloat(value);
      return value.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
    },
  },
  created: function () {
    evntBus.$on('open_mpesa_payments', (data) => {
      this.dialog = true;
      this.full_name = '';
      this.mobile_no = '';
      this.company = data.company;
      this.customer = data.customer;
      this.mode_of_payment = data.mode_of_payment;
      this.dialog_data = '';
      this.selected = [];
    });
  },
};
